* {
  margin: 0;
  padding: 0;
}
nav {
  ul {
    list-style-type: none;
    display: flex;
    justify-content: flex-end;
    align-items: center;
    height: 50px;
    width: 100%;
    background-color: #dedede;
    position: fixed;
    left: 0;
    top: 0;
  }

  li {
    margin: 10px;
    padding: 5px 5px;
  }

  li:hover {
    background-color: #eeeeee;
  }

  a {
    text-decoration: none;
    color: #000000;
  }

}